//-------------------importation des variables-------------------------
@import"../variables";
//-------------------importation des mixins----------------------------
@import"../mixins";

.home__Contenair{
    margin: 0;
    position: relative;
    height: 100%;
}
.banner_container{
    margin-left: 40px;
    margin-right: 40px;
    height:223px;
    margin-top: 20px;

    @media only screen and (max-width: 768px) {
        margin-left: 20px;
        margin-right: 20px;
    }
    @media screen and(max-width:568px){
        height: 111px;
    }

    &_img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        @include big-radius; 
    
    
        @media only screen and (min-width: 768px) and (max-width: 1023px) {//768::568
            @include small-radius;
        }
        @media only screen and (min-width: 568px) and (max-width: 768px) {//768
            @include small-radius;
        }
        @media screen and(max-width:568px){      
            @include small-radius;
        }
    }
    &_text{
        @include flex-center;
        color: $tertiary-color;
        overflow: hidden;
        @include font(48px, 500);
        line-height:68.45px;
        margin-top: -150px;
        z-index: 2;

        @media only screen and (min-width: 796px) and (max-width: 1023px) {//768::568
            @include font(42px, 500)    
        }
        @media only screen and (min-width: 568px) and (max-width:796px) {//768
            @include font(32px, 500);
  
        }
        @media only screen and (min-width: 796px) and (max-width:568px) {//768
            @include font(24px, 500);

            justify-content:flex-start;
            width:217px;
            height:48px;
            margin-top: -80px;
            margin-left:20px;
            @include font(24px, 500);
            line-height:24px; 
  
        }
        @media screen and(max-width:568px){  
            justify-content:flex-start;
            width:217px;
            height:48px;
            margin-top: -80px;
            margin-left:20px;
            @include font(24px, 500);
            line-height:24px; 
        }  
    }    
}
.cards{
    position: relative;
    @include big-radius; 
    background-color:#F6F6F6;
    height: 100%;
    margin-left:40px;
    margin-right: 40px;
    margin-top:40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    @media only screen and (min-width:768px) and (max-width: 1023px) {//768::568
        grid-template-columns: repeat(2, 1fr);
        @include small-radius;
        background-color:#F6F6F6;
        margin-top:20px;
        height: 100%;
    }

    @media only screen and (min-width: 568px) and (max-width:768px) {//768
        grid-template-columns: repeat(2, 1fr);
        margin-left:20px;
        margin-right: 20px;
        @include small-radius;
        background-color:#F6F6F6;
    }
    @media screen and(max-width:568px){
        grid-template-columns: repeat(1, 1fr);
        margin-left:20px;
        margin-right: 20px;
        @include small-radius;
        background-color:white;
        grid-gap: 20px;
        margin-top:20px;
    }
}
.card{
    position: relative;
    display: flex;
    flex-direction: column;
    @include small-radius;
    margin: 20px;
    text-decoration:none;
    @media only screen and (min-width: 568px) and (max-width: 768px) {//768
        margin: 20px;
    }
    @media screen and(max-width:568px){
        margin: 0px;
        height: 335px;
        max-width: 100%;
    }
  
    &_img{
        height: 100%;
        width: 100%;
        @include small-radius;
        z-index:1 ;
        object-fit: cover;
    }
    &_layer{
        position:absolute;
        width: 100%;
        bottom:0px;
        @include small-radius;
        cursor: pointer;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);      
        z-index: 2;       
        &_title{
            padding:  0px 10px 0px 20px;
            margin-top: 50%;
            line-height:25,67px;
            color:$tertiary-color;
            @include font(26px, 500);
            overflow: hidden;
            z-index: 33;   
            @media only screen and (min-width: 1260px) and (max-width: 1441px) {//768
                @include font(16px, 500);
                width: 60%;
                margin-top: 50%;
            }
            @media only screen and (min-width: 796px) and (max-width: 1260px) {//768
                @include font(16px, 500);
                width: 80%;
                margin-top: 50%;
      
            }
            @media only screen and (min-width: 568px) and (max-width:796px) {//768
                @include font(16px, 500);
                width:80%;
                margin-top: 50%;
            }
            @media only screen and (min-width: 375px) and (max-width:568px) {//768
                @include font(14px, 500);
                width:60%;
            }

            @media screen and(max-width: 375px){
                @include font(14px, 500);
                width:70%;
                margin-top:85% ;
 
            }
        }
       
    }
}
.card:hover{
    box-shadow: 4px 2px rgba(246, 246, 246, 1);
}
   

